import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { IS_ADMIN, hideAdmin, toggleAdmin } from "../store";
import { tshirtColors } from "../theme";

const Panel = styled.div`
  position: fixed;
  top: 0;
  left: calc(50vw - 160px);
  z-index: 100;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 320px;
  height: 22px;
  background: ${tshirtColors.blue2};
  font-size: 15px;
  line-height: 18px;
  color: #fff;

  & > button {
    all: unset;
    display: block;
    opacity: 1;
    text-align: center;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }
`;

const Admin = ({ isAdminHidden, toggleAdmin, hideAdmin }) =>
  IS_ADMIN &&
  !isAdminHidden && (
    <Panel>
      <button onClick={() => toggleAdmin(true)}>Одмин</button>
      <div>|</div>
      <button onClick={() => toggleAdmin(false)}>Ниадмин</button>
      <div>|</div>
      <button onClick={hideAdmin}>Скрыть</button>
    </Panel>
  );

export default connect(
  state => state,
  { toggleAdmin, hideAdmin }
)(Admin);
