import React from "react";
import Button from "@material-ui/core/es/Button/Button";
import { DistrictSelect, PropertySelector } from "../../components/controls";
import { colors, districts, sizes } from "../../../data";
import OutlinedInput from "@material-ui/core/es/OutlinedInput/OutlinedInput";
import { connect } from "react-redux";
import { withYmGoal } from "../../utils";

const Form = ({
  onClick,
  selectDistrict,
  selectColor,
  selectSize,
  selectForeign,
  buy,
  district,
  color,
  size,
  foreignCities,
  foreign,
  isAdmin,
  setRotate,
  rotate
}) => {
  const withRotate = cb => val => {
    cb(val);
    !isAdmin && setRotate(Math.round(25 * (Math.random() - 0.5)));
  };
  return (
    <div onClick={onClick}>
      <DistrictSelect
        value={district}
        values={districts.map(d => d.toLowerCase())}
        select={withRotate(selectDistrict)}
      />
      <PropertySelector
        selected={foreign}
        values={foreignCities}
        select={withRotate(selectForeign)}
      />
      <PropertySelector
        selected={color}
        values={colors}
        select={withRotate(selectColor)}
      />
      <PropertySelector
        selected={size}
        values={sizes}
        select={withRotate(selectSize)}
      />
      {isAdmin && (
        <OutlinedInput
          labelWidth={100}
          value={rotate}
          onChange={e => setRotate(e.target.value)}
          type="number"
          style={{ marginLeft: "20px", marginBottom: "8px" }}
        />
      )}
      <Button
        onClick={withYmGoal(buy, "BK_BUY")}
        variant="contained"
        color="primary"
      >
        {`${isAdmin ? "Скачать" : `${window.txtlPrice}₽ Купить`}`}
      </Button>
    </div>
  );
};

export default connect(({ isAdmin }) => ({ isAdmin }))(Form);
