// @flow

import { saveAs } from "file-saver";

export const isInViewPort = ({
  current: element
}: {
  current: HTMLElement | null
}) => {
  if (element) {
    const rect = element.getBoundingClientRect();

    return rect.top < window.innerHeight && rect.bottom > 0;
  } else return false;
};

export const saveSvgFile = (name: string, print: string) =>
  saveAs(new Blob([print]), name);

export const withYmGoal = (cb: any, goal: string) => (...val: any) => {
  try {
    window.yaCounter49597159.reachGoal(goal);
  } catch (e) {
    console.error("there is a problem with yandex metrika");
  }
  return cb(...val);
};
