import React from "react";
import Button from "@material-ui/core/es/Button/Button";
import { DistrictSelect, PropertySelector } from "../../components/controls";
import { colors, districts, sizes } from "../../../data";
import { connect } from "react-redux";
import { withYmGoal } from "../../utils";

const Form = ({
  onClick,
  selectDistrict,
  selectColor,
  selectSize,
  buy,
  district,
  color,
  size,
  isAdmin
}) => (
  <div onClick={onClick}>
    <DistrictSelect
      value={district}
      values={districts.map(d => d.toLowerCase())}
      select={selectDistrict}
    />
    <PropertySelector selected={color} values={colors} select={selectColor} />
    <PropertySelector selected={size} values={sizes} select={selectSize} />
    <Button
      onClick={withYmGoal(buy, "DOSHIRAK_BUY")}
      variant="contained"
      color="primary"
    >
      {`${isAdmin ? "Скачать" : `${window.txtlPrice}₽ Купить`}`}
    </Button>
  </div>
);

export default connect(({ isAdmin }) => ({ isAdmin }))(Form);
