import { createStore } from "redux";
import { createAction, createReducer } from "deox";
import update from "immutability-helper";

const ADMIN_NAME = "marketplace";

export const toggleAdmin = createAction("TOGGLE_ADMIN", resolve => isAdmin =>
  resolve(isAdmin)
);

export const hideAdmin = createAction("HIDE_ADMIN");

export const IS_ADMIN =
  !!window.txtlAdminPage && localStorage.getItem("txtl_user") === ADMIN_NAME;

const reducer = createReducer(
  { isAdmin: IS_ADMIN, isAdminHidden: !IS_ADMIN },
  handle => [
    handle(toggleAdmin, (state, { payload }) =>
      update(state, { $set: { isAdmin: payload } })
    ),
    handle(hideAdmin, state => update(state, { $set: { isAdminHidden: true } }))
  ]
);

export const store = createStore(reducer);
